import React, { useEffect } from "react";
import { connect } from "react-redux";
import { setCategory } from "../actions/auth";

const About = ({ setCategory }) => {
  useEffect(() => {
    setCategory("About");
    return () => setCategory("Categories");
  }, [setCategory]);
  return (
    <p className="about">
      Hello, this is Ahmed. This site was inspired by{" "}
      <a href="https://www.chaldal.com">Chaldal.Com</a>, a very good online
      store and well functioning web app. Thought trying to replicate this would
      be a good way to practice Django, React, managing e-commerce
      functionalities, related database management, authentication and stuff.
      Check out my portfolio{" "}
      <a href="https://fayeazahmed.github.io/portfolio/">@fayeazahmed</a>
    </p>
  );
};

export default connect(null, { setCategory })(About);
