import axios from "axios";
import { BASE_URL } from "../components/config";
const { setAlert } = require("./alert");
const {
  SIGNIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  SET_CATEGORY,
  ADD_TO_CART,
  REMOVE_FROM_CART,
  SET_USER,
  CREATE_ORDER,
} = require("./types");

export const setHeader = (token = localStorage.getItem("token")) =>
  (axios.defaults.headers.common["Authorization"] = `Bearer ${token}`);

export const setCategory = (category) => async (dispatch) => {
  function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
  dispatch({
    type: SET_CATEGORY,
    payload: capitalizeFirstLetter(category),
  });
};

export const addToCart = (product, inCart) => async (dispatch) => {
  dispatch({
    type: ADD_TO_CART,
    payload: {
      product,
      inCart,
    },
  });
};

export const removeFromCart = (inCartId, removeOne) => async (dispatch) => {
  dispatch({
    type: REMOVE_FROM_CART,
    payload: {
      inCartId,
      removeOne,
    },
  });
};

export const login = (email, password) => async (dispatch) => {
  try {
    const response = await axios.post(`${BASE_URL}/api/users/signinorup`, {
      email,
      password,
    });
    if (response.data) {
      const token = await axios.post(`${BASE_URL}/api/token/`, {
        email,
        password,
      });
      setHeader(token.data.access);
      dispatch({
        type: SIGNIN_SUCCESS,
        payload: token.data,
      });
      localStorage.setItem("userId", response.data.id);
      dispatch(setAlert("Authenticated successfully", "success"));
      dispatch(setUser());
      return true;
    } else {
      const user = await axios.post(`${BASE_URL}/api/users/signup`, {
        email,
        password,
      });
      if (user.data.status === 411) {
        dispatch(setAlert("Password must be at least 6 characters", "danger"));
      } else {
        localStorage.setItem("userId", user.data.user.id);
        dispatch(login(email, password));
        dispatch(setAlert("Account created successfully", "success"));
        return true;
      }
    }
  } catch (error) {
    console.log(error.response);
    dispatch({
      type: LOGIN_FAIL,
    });
    dispatch(setAlert("Wrong credentials!", "danger"));
  }
};

export const setUser = (userId = localStorage.getItem("userId")) => async (
  dispatch
) => {
  try {
    const response = await axios.get(`${BASE_URL}/api/users/${userId}`);
    dispatch({
      type: SET_USER,
      payload: response.data,
    });
  } catch (error) {
    dispatch(setAlert("Authentication error", "danger"));
  }
};

export const updateProfile = (formData) => async (dispatch) => {
  try {
    await axios.post(`${BASE_URL}/api/users/profile`, formData);
    dispatch(setAlert("Profile updated", "success"));
  } catch (error) {
    if (error.response.status === 401) {
      dispatch(setAlert("Unauthorized! Log in again", "danger"));
    } else {
      dispatch(setAlert("Something went wrong", "warning"));
    }
    dispatch({
      type: LOGIN_FAIL,
    });
  }
};

export const createOrder = (formData) => async (dispatch) => {
  try {
    await axios.post(`${BASE_URL}/api/orders/create`, formData);
    dispatch(setAlert("Order has been added", "success"));
    dispatch({
      type: CREATE_ORDER,
    });
  } catch (error) {
    dispatch(setAlert("Something went wrong", "warning"));
  }
};

export const retrieveOrders = (email) => async (dispatch) => {
  try {
    const response = await axios.post(`${BASE_URL}/api/orders/retrieve`, {
      email,
    });
    return response.data;
  } catch (error) {
    dispatch(setAlert("Something went wrong", "warning"));
    dispatch({
      type: LOGIN_FAIL,
    });
  }
};

export const logout = () => (dispatch) => {
  dispatch(setAlert("Logged out", "success"));
  dispatch({
    type: LOGOUT,
  });
};
