import "./App.css";
import { Provider } from "react-redux";
import { HashRouter, Route, Switch } from "react-router-dom";
import store from "./store";
import HomePage from "./components/HomePage";

function App() {
  return (
    <Provider store={store}>
      <HashRouter>
        <Switch>
          <Route path="/" component={HomePage} />
        </Switch>
      </HashRouter>
    </Provider>
  );
}

export default App;
