import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { createOrder, setCategory, setHeader } from "../actions/auth";
import { connect } from "react-redux";
import { setAlert } from "../actions/alert";
import { Link } from "react-router-dom";

const Checkout = ({
  cart,
  setCategory,
  setAlert,
  user,
  setSignInModalOpen,
  createOrder,
}) => {
  const [total, setTotal] = useState(null);
  const [comments, setComments] = useState("");
  const [preferred_time, setPreferredTime] = useState("Morning");
  const [orderComplete, setOrderComplete] = useState(false);

  useEffect(() => {
    localStorage.getItem("token") && setHeader();
    let total = 0;
    cart &&
      cart.forEach((item) => {
        total += item.price * item.inCart;
      });
    setTotal(total);
    setCategory("Checkout");
    !localStorage.getItem("token") &&
      setAlert("You have to login to check out", "info");
  }, [setCategory, setAlert, cart]);

  const handleSubmit = () => {
    const products = [];
    cart.forEach(({ id, inCart: quantity }) => products.push({ id, quantity }));
    const formData = {
      email: user.email,
      comments,
      total,
      preferred_time,
      products,
    };
    createOrder(formData);
    setOrderComplete(true);
  };

  if (cart.length < 1)
    return (
      <div className="emptyCart w-100">
        {orderComplete && (
          <p>
            The order is successfully placed, you'll hear from us soon.{" "}
            <a href="/index.html">Continue shopping!</a>
          </p>
        )}
        <img
          className="mt-3 mx-auto"
          src={require("../assets/emptyShoppingBag.jpg").default}
          alt="no items"
        />
      </div>
    );

  return (
    <div className="checkOut text-left mt-2">
      {user ? (
        <>
          <div className="checkOut__address d-flex align-items-center">
            <i className="fa fa-map-marker" aria-hidden="true"></i>
            <p className="mb-0 ml-2">
              <b>Delivery Address</b>
            </p>
            <p className="mb-0 ml-2">{user.address}</p>
            <Link to="/profile" className="btn btn-sm btn-outline-danger">
              Change
            </Link>
          </div>
          <div className="checkOut__timings mt-5">
            <div className="d-flex align-items-center">
              <i className="fa fa-clock-o" aria-hidden="true"></i>
              <p className="mb-0 ml-2">
                <b>Preferred Delivery Timings</b>
              </p>
            </div>
            <div className="d-flex justify-content-between align-items-center">
              <p className="mb-0">When would you like regular Delivery</p>
              <select
                defaultValue={preferred_time}
                className="ml-3 p-3 btn btn-danger"
                onChange={(e) => setPreferredTime(e.target.value)}
              >
                <option value="Morning">Morning</option>
                <option value="Evening">Evening</option>
              </select>
            </div>
            <textarea
              className="text-left"
              value={comments}
              name="comments"
              placeholder="Add any extra instruction..."
              onChange={(e) => setComments(e.target.value)}
            ></textarea>
          </div>
          <div className="d-flex flex-column align-items-end mt-2">
            <button
              onClick={handleSubmit}
              className="checkOut__confirmBtn btn btn-lg btn-danger"
            >
              Proceed{" "}
              {user.successful_orders >= 10 ? total - total * 0.1 : total} ৳
            </button>
            {user.successful_orders >= 10 && (
              <span className="badge badge-success mt-2">10% DISCOUNT!</span>
            )}
          </div>
        </>
      ) : (
        <>
          {" "}
          <h3>You have to be logged in to complete the order.</h3>
          <button
            onClick={() => setSignInModalOpen(true)}
            className="checkOut__signinBtn btn btn-danger btn-lg mt-2"
          >
            Log in or sign up
          </button>
        </>
      )}
    </div>
  );
};

const mapStateToProps = (state) => ({
  cart: state.auth.cart,
  user: state.auth.user,
});

Checkout.propTypes = {
  setCategory: PropTypes.func.isRequired,
  cart: PropTypes.array.isRequired,
  setAlert: PropTypes.func.isRequired,
  createOrder: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, { setCategory, setAlert, createOrder })(
  Checkout
);
