import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart";
import RemoveShoppingCartIcon from "@material-ui/icons/RemoveShoppingCart";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import { addToCart, removeFromCart } from "../actions/auth";
import { v4 } from "uuid";
import { BASE_URL } from "./config";

const Item = ({ product, addToCart, removeFromCart }) => {
  const { description, name, photo, price, in_stock } = product;
  const [inCart, setInCart] = useState(false);
  const [inCartId, setInCartId] = useState(null);
  useEffect(() => {
    const cart = JSON.parse(localStorage.getItem("chaldalCart"));
    if (cart) {
      for (const item of cart) {
        if (item.id === product.id) setInCart(true);
        setInCartId(item.inCartId);
      }
    }
  }, [product]);

  const handleClick = () => {
    if (!inCart) {
      const cartId = v4();
      setInCartId(cartId);
      product["inCartId"] = cartId;
      addToCart(product, inCart);
      setInCart(true);
    } else {
      removeFromCart(inCartId, false);
      setInCart(false);
    }
  };

  return (
    <Card className="item-list__card">
      <CardContent>
        <CardMedia
          className="item-list__image"
          image={BASE_URL + "/static" + photo.replace("/media", "")}
          title={name}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
      </CardContent>
      <div className="d-flex justify-content-between align-items-center">
        <p className="item-list__price">{price}৳</p>
        {in_stock ? (
          <IconButton
            onClick={handleClick}
            color={inCart ? "secondary" : "primary"}
            aria-label="add to shopping cart"
          >
            {inCart ? (
              <RemoveShoppingCartIcon className="item-list__cart-icon" />
            ) : (
              <AddShoppingCartIcon className="item-list__cart-icon" />
            )}
          </IconButton>
        ) : (
          <div className="soldout-container">Out of Stock :(</div>
        )}
      </div>
    </Card>
  );
};

Item.propTypes = {
  product: PropTypes.object.isRequired,
  addToCart: PropTypes.func.isRequired,
  removeFromCart: PropTypes.func.isRequired,
};

export default connect(null, { addToCart, removeFromCart })(Item);
