import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import ReactLoading from "react-loading";
import { setCategory } from "../actions/auth";
import axios from "axios";
import Item from "./Item";
import { BASE_URL } from "./config";

const ItemList = ({ match, setCategory }) => {
  const [products, setProducts] = useState([]);
  useEffect(() => {
    setCategory(match.params.itemCategory);
    axios
      .get(`${BASE_URL}/api/products/${match.params.itemCategory}`)
      .then((res) => setProducts(res.data))
      .catch((e) => console.log(e));

    return () => {
      setCategory("Categories");
    };
  }, [match.params.itemCategory, setCategory]);

  if (products.length === 0)
    return (
      <ReactLoading
        className="m-auto"
        type="balls"
        color="#fdd670"
        height={80}
        width={80}
      />
    );
  return (
    <div className="item-list d-flex flex-wrap">
      {products.map((product) => (
        <Item key={product.id} product={product} />
      ))}
    </div>
  );
};

const mapStateToProps = (state) => ({
  category: state.auth.category,
});

ItemList.propTypes = {
  category: PropTypes.string.isRequired,
  setCategory: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, { setCategory })(ItemList);
