import React from "react";
import PropTypes from "prop-types";
import { addToCart, removeFromCart } from "../actions/auth";
import { connect } from "react-redux";
import { BASE_URL } from "./config";

const CartItem = ({ item, addToCart, removeFromCart }) => {
  const { id, name, inCart, photo, price, inCartId } = item;
  return (
    <div className="cart-item">
      <div className="cart-item__btns d-flex flex-column">
        <button
          onClick={() => addToCart({ id }, true)}
          className="btn btn-sm rounded-circle"
        >
          <i className="fa fa-angle-up" aria-hidden="true"></i>
        </button>
        <p className="text-center">{inCart}</p>
        <button
          onClick={() =>
            inCart === 1
              ? removeFromCart(inCartId, false)
              : removeFromCart(inCartId, true)
          }
          className="btn btn-sm rounded-circle"
        >
          <i className="fa fa-angle-down" aria-hidden="true"></i>
        </button>
      </div>
      <img
        src={BASE_URL + "/static" + photo.replace("/media", "")}
        alt={name}
        className="cart-item__image ml-2"
      />
      <div className="cart-item__details">
        <p>{name}</p>
        <p>{price}৳</p>
      </div>
      <p>{price * inCart}৳</p>
      <button
        onClick={() => removeFromCart(inCartId, false)}
        className="cart-item__remove-btn btn btn-sm text-secondary"
      >
        <i className="fa fa-times fa-2x" aria-hidden="true"></i>
      </button>
    </div>
  );
};

CartItem.propTypes = {
  addToCart: PropTypes.func.isRequired,
  removeFromCart: PropTypes.func.isRequired,
};

export default connect(null, { addToCart, removeFromCart })(CartItem);
