import axios from "axios";
const {
  SIGNUP_FAIL,
  SIGNIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  SET_CATEGORY,
  ADD_TO_CART,
  REMOVE_FROM_CART,
  SET_USER,
  CREATE_ORDER,
} = require("../actions/types");

const initialState = {
  token: localStorage.getItem("token"),
  isAuthenticated: null,
  category: "Not Chaldal",
  cart: JSON.parse(localStorage.getItem("chaldalCart")) || [],
  user: null,
};

const authState = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SET_USER:
      return {
        ...state,
        isAuthenticated: true,
        user: payload,
      };
    case SIGNIN_SUCCESS:
      localStorage.setItem("token", payload.access);

      return {
        ...state,
        isAuthenticated: true,
        token: payload.access,
      };

    case SIGNUP_FAIL:
    case LOGIN_FAIL:
    case LOGOUT:
      delete axios.defaults.headers.common["Authorization"];
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      return {
        ...state,
        token: null,
        isAuthenticated: null,
        user: null,
      };

    case SET_CATEGORY:
      return {
        ...state,
        category: payload,
      };

    case ADD_TO_CART:
      const { product, inCart } = payload;
      let newCartAdd = [...state.cart];
      if (!inCart) {
        product["inCart"] = 1;
        newCartAdd.push(product);
        localStorage.setItem("chaldalCart", JSON.stringify(newCartAdd));
        return {
          ...state,
          cart: [...state.cart, product],
        };
      } else {
        newCartAdd = newCartAdd.map((item) => {
          if (item.id === product.id) {
            item.inCart++;
          }
          return item;
        });
        localStorage.setItem("chaldalCart", JSON.stringify(newCartAdd));
        return {
          ...state,
          cart: state.cart.map((item) =>
            item.id === product.id ? { ...item, inCart: item.inCart++ } : item
          ),
        };
      }

    case REMOVE_FROM_CART:
      const { inCartId, removeOne } = payload;
      let newCartRemove = [...state.cart];
      if (!removeOne) {
        newCartRemove = newCartRemove.filter(
          (item) => item.inCartId !== inCartId
        );
        localStorage.setItem("chaldalCart", JSON.stringify(newCartRemove));
        return {
          ...state,
          cart: state.cart.filter((item) => item.inCartId !== inCartId),
        };
      } else {
        newCartRemove = newCartRemove.map((item) => {
          if (item.inCartId === inCartId) {
            item.inCart--;
          }
          return item;
        });
        localStorage.setItem("chaldalCart", JSON.stringify(newCartRemove));
        return {
          ...state,
          cart: state.cart.map((item) =>
            item.inCartId === inCartId
              ? { ...item, inCart: item.inCart-- }
              : item
          ),
        };
      }

    case CREATE_ORDER:
      localStorage.removeItem("chaldalCart");
      return {
        ...state,
        cart: [],
      };

    default:
      return state;
  }
};

export default authState;
