import React, { Fragment } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Link, Route } from "react-router-dom";
import ItemList from "./ItemList";
import { setCategory } from "../actions/auth";
import Checkout from "./Checkout";
import Profile from "./Profile";
import PrivateRoute from "./PrivateRoute";
import OrderHistory from "./OrderHistory";
import About from "./About";

const Main = ({ category, setSignInModalOpen }) => {
  return (
    <Fragment>
      <h1>{category}</h1>
      <div className="categories d-flex flex-wrap mt-3">
        <Route exact path="/">
          <Link to="/food">
            <img
              src={require("../assets/categories/food.jpg").default}
              alt="food"
            />
          </Link>
          <Link to="/health">
            <img
              src={require("../assets/categories/health.png").default}
              alt="health"
            />
          </Link>
          <Link to="/office">
            <img
              src={require("../assets/categories/office.jpg").default}
              alt="office"
            />
          </Link>
        </Route>
        <Route exact path="/food">
          <Link to="/category/fruits">
            <img
              src={require("../assets/categories/fruits.jpg").default}
              alt="fruits"
            />
          </Link>
          <Link to="/category/meat">
            <img
              src={require("../assets/categories/meat.jpg").default}
              alt="meat"
            />
          </Link>
        </Route>
        <Route exact path="/health">
          <Link to="/category/health">
            <img
              src={require("../assets/categories/healthcare.jpg").default}
              alt="healthcare"
            />
          </Link>
          <Link to="/category/personal">
            <img
              src={require("../assets/categories/personal.jpg").default}
              alt="personal"
            />
          </Link>
        </Route>
        <Route exact path="/office">
          <Link to="/category/battery">
            <img
              src={require("../assets/categories/batteries.jpg").default}
              alt="batteries"
            />
          </Link>
          <Link to="/category/organizing">
            <img
              src={require("../assets/categories/organizing.jpg").default}
              alt="organizing"
            />
          </Link>
        </Route>
        <Route exact path="/category/:itemCategory" component={ItemList} />
        <Route exact path="/checkout">
          <Checkout setSignInModalOpen={setSignInModalOpen} />
        </Route>
        <PrivateRoute exact path="/profile" component={Profile} />
        <PrivateRoute exact path="/orders" component={OrderHistory} />
        <Route exact path="/about" component={About} />
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  category: state.auth.category,
});

Main.propTypes = {
  category: PropTypes.string.isRequired,
  setCategory: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, { setCategory })(Main);
