import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import CartItem from "./CartItem";
import { Link } from "react-router-dom";

const Cart = ({ cartOpen, setCartOpen, cart }) => {
  const [total, setTotal] = useState(0);

  useEffect(() => {
    let total = 0;
    cart.forEach((item) => {
      total += item.price * item.inCart;
    });
    setTotal(total);
  }, [cart]);

  return (
    <div className={`cart transition ${cartOpen ? "" : "width-zero"}`}>
      <div className="cart__hero">
        <p className="mb-0 ml-2">
          {cart.length} ITEMS{" "}
          <i className="fa fa-shopping-bag" aria-hidden="true"></i>
        </p>
        <button
          onClick={() => setCartOpen(false)}
          className="btn btn-sm btn-primary mr-2"
        >
          Close
        </button>
      </div>
      <div
        onClick={() => setCartOpen(!cartOpen)}
        className={`cart__stickyBtn transition ${cartOpen ? "" : "right-zero"}`}
      >
        {!cartOpen ? (
          <>
            <i className="fa fa-shopping-cart fa-3x" aria-hidden="true"></i>
            <p className="d-inline-block">{cart.length}</p>
            <p className="m-0">{total} ৳</p>
          </>
        ) : (
          <i className="fa fa-angle-right fa-2x" aria-hidden="true"></i>
        )}
      </div>
      {cart.length < 1 ? (
        <div>
          <img
            className="mt-5"
            src={require("../assets/emptyShoppingBag.jpg").default}
            alt="no items"
          />
          <p className="mt-2">Your shopping bag is empty. Start shopping</p>
        </div>
      ) : (
        <div className="cart__contents d-flex flex-column">
          {cart.map((item) => (
            <CartItem item={item} key={item.inCartId} />
          ))}
          <Link
            onClick={() => setCartOpen(false)}
            to="/checkout"
            className="cart__orderbtn btn btn-danger mt-2"
          >
            <p>Place Order</p>
            <p>{total}৳</p>
          </Link>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = (state) => ({
  cart: state.auth.cart,
});

Cart.propTypes = {
  cart: PropTypes.array.isRequired,
};

export default connect(mapStateToProps, null)(Cart);
