import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { retrieveOrders, setCategory, setHeader } from "../actions/auth";
import { connect } from "react-redux";
import ReactLoading from "react-loading";
import axios from "axios";
import { BASE_URL } from "./config";

const OrderHistory = ({ setCategory, user, retrieveOrders }) => {
  const [orders, setOrders] = useState(null);
  const [selectedOrder, setSelectedOrder] = useState(null);
  useEffect(() => {
    setCategory("Order History");
    setHeader();
    user && setOrderState();
    async function setOrderState() {
      const result = await retrieveOrders(user.email);
      setOrders(result);
    }
  }, [setCategory, user, retrieveOrders]);

  const handleOrderDetail = async (id) => {
    const res = await axios.post(
      `${BASE_URL}/api/orderedproducts/retrieveorder`,
      { id }
    );
    setSelectedOrder(res.data);
  };

  if (!orders)
    return (
      <ReactLoading
        className="m-auto"
        type="balls"
        color="#fdd670"
        height={80}
        width={80}
      />
    );

  return orders.length < 1 || !user ? (
    <h2>No order history yet!</h2>
  ) : (
    <div className="orderHistory__container d-flex">
      <div className="orderHistory d-flex flex-column">
        {orders.map(
          ({ id, delivered, total, date_ordered, preferred_time }) => (
            <div
              key={id}
              className="orderHistory__item d-flex justify-content-between"
              onClick={() => handleOrderDetail(id)}
            >
              <div>
                <span
                  className={`badge badge-${delivered ? "success" : "warning"}`}
                >
                  {delivered ? "Complete" : "Processing"}
                </span>
                <p>
                  Order <b>#{id}</b>
                </p>
                <p>{total} ৳</p>
              </div>
              <div>
                <p>{date_ordered}</p>
                <p>{preferred_time}</p>
              </div>
            </div>
          )
        )}
        <h3 className="orderHistory__discount">
          {user.successful_orders < 10
            ? `Order ${
                10 - user.successful_orders
              } more times to get 10% discount`
            : "You are enojoying your 10% discount on check out"}
        </h3>
      </div>
      {selectedOrder && selectedOrder.length > 0 && (
        <div className="orderHistory__products d-flex flex-column">
          <button
            onClick={() => setSelectedOrder(null)}
            className="orderHistory__closeBtn btn btn-outline-secondary btn-sm"
            type="button"
          >
            X
          </button>
          {selectedOrder.map(({ id, name, price, photo }) => (
            <div key={id} className="row">
              <img
                className="col-3"
                src={BASE_URL + "/static" + photo.replace("/media", "")}
                alt={name}
              />
              <h4 className="col-6 text-left">
                <b>{name}</b>
              </h4>
              <p className="col-3">{price} ৳</p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

const mapStateToProps = (state) => ({
  user: state.auth.user,
});

OrderHistory.propTypes = {
  setCategory: PropTypes.func.isRequired,
  retrieveOrders: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, {
  setCategory,
  retrieveOrders,
})(OrderHistory);
