import React, { useState } from "react";
import { Link } from "react-router-dom";

const Menu = ({ isOpen, mobileBurger, setMobileBurger }) => {
  const [activatedMenu, setActivateMenu] = useState("None");
  return (
    <div
      className={`menu transition ${isOpen ? "" : "translate-menu"} ${
        mobileBurger ? "translate-zero" : ""
      }`}
    >
      <ul>
        <li
          onClick={(e) =>
            activatedMenu === e.currentTarget.getAttribute("menu-category")
              ? setActivateMenu("")
              : setActivateMenu(e.currentTarget.getAttribute("menu-category"))
          }
          menu-category="Food"
          className={`menu__main ${
            activatedMenu === "Food" ? "menu__main--activated" : ""
          }`}
        >
          <div>
            <i className="fa fa-cutlery" aria-hidden="true"></i>Food
          </div>
          <ul>
            <Link onClick={() => setMobileBurger(false)} to="/category/fruits">
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-apple" aria-hidden="true"></i>Fruit and
                  Vegetables
                </div>
              </li>
            </Link>
            <Link onClick={() => setMobileBurger(false)} to="/category/meat">
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-ship" aria-hidden="true"></i>Meat and Fish
                </div>
              </li>
            </Link>
          </ul>
        </li>
        <li
          onClick={(e) =>
            activatedMenu === e.currentTarget.getAttribute("menu-category")
              ? setActivateMenu("")
              : setActivateMenu(e.currentTarget.getAttribute("menu-category"))
          }
          menu-category="Health"
          className={`menu__main ${
            activatedMenu === "Health" ? "menu__main--activated" : ""
          }`}
        >
          <div>
            <i className="fa fa-medkit" aria-hidden="true"></i>Beauty and Health
          </div>
          <ul>
            <Link onClick={() => setMobileBurger(false)} to="/category/health">
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-h-square" aria-hidden="true"></i>Health
                  Care
                </div>
              </li>
            </Link>
            <Link
              onClick={() => setMobileBurger(false)}
              to="/category/personal"
            >
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-heart" aria-hidden="true"></i>Personal
                  Care
                </div>
              </li>
            </Link>
          </ul>
        </li>
        <li
          onClick={(e) =>
            activatedMenu === e.currentTarget.getAttribute("menu-category")
              ? setActivateMenu("")
              : setActivateMenu(e.currentTarget.getAttribute("menu-category"))
          }
          menu-category="Office"
          className={`menu__main ${
            activatedMenu === "Office" ? "menu__main--activated" : ""
          }`}
        >
          <div>
            <i className="fa fa-briefcase" aria-hidden="true"></i>Office
            Products
          </div>
          <ul>
            <Link onClick={() => setMobileBurger(false)} to="/category/battery">
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-database" aria-hidden="true"></i>Batteries
                </div>
              </li>
            </Link>
            <Link
              onClick={() => setMobileBurger(false)}
              to="/category/organizing"
            >
              <li className="menu__secondary">
                <div>
                  <i className="fa fa-file" aria-hidden="true"></i>Organizing
                </div>
              </li>
            </Link>
          </ul>
        </li>
      </ul>
    </div>
  );
};

export default Menu;
