import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import {
  setCategory,
  updateProfile,
  setHeader,
  setUser,
} from "../actions/auth";
import { connect } from "react-redux";
import { setAlert } from "../actions/alert";

const Profile = ({ setCategory, user, updateProfile, setUser }) => {
  setHeader();
  const [submitBtn, setSubmitBtn] = useState(false);
  const [formData, setFormData] = useState({
    id: "",
    email: "",
    userName: "",
    phone: "",
    address: "",
  });

  const handleChange = (name, value) => {
    setFormData({
      ...formData,
      [name]: value,
    });
    setSubmitBtn(true);
  };

  useEffect(() => {
    user &&
      setFormData({
        id: user.id,
        email: user.email,
        userName: user.name || "Enter information",
        phone: user.phone || "Enter information",
        address: user.address || "Enter information",
      });
    setCategory("Profile");
    return () => {
      setCategory("Categories");
    };
  }, [user, setCategory]);

  if (user) {
    const { userName, email, phone, address } = formData;
    return (
      <form
        className="profileForm"
        onSubmit={(e) => {
          e.preventDefault();
          updateProfile(formData);
          setSubmitBtn(false);
          setTimeout(setUser, 800);
        }}
      >
        {!submitBtn && (
          <button
            onClick={() => setSubmitBtn(true)}
            className="btn btn-sm btn-info float-right"
            type="button"
          >
            <i className="fa fa-pencil" aria-hidden="true"></i>
          </button>
        )}
        <fieldset disabled={!submitBtn && "disabled"}>
          <p>Name</p>
          <input
            onChange={(e) => handleChange(e.target.name, e.target.value)}
            placeholder={userName}
            name="userName"
            className=""
            type="text"
            value={userName}
          />
          <p>Email</p>
          <input
            disabled
            placeholder={email}
            name="email"
            type="email"
            value={email}
          />
          <p>Phone</p>
          <input
            onChange={(e) => handleChange(e.target.name, e.target.value)}
            placeholder={phone}
            name="phone"
            type="text"
            value={phone}
          />
          <p>Address</p>
          <input
            onChange={(e) => handleChange(e.target.name, e.target.value)}
            placeholder={address}
            name="address"
            type="text"
            value={address}
          />
        </fieldset>
        {submitBtn && (
          <button type="submit" className="btn btn-danger btn-lg d-block mt-4">
            Update
          </button>
        )}
      </form>
    );
  } else return null;
};

const mapStateToProps = (state) => ({
  user: state.auth.user,
});

Profile.propTypes = {
  setCategory: PropTypes.func.isRequired,
  setAlert: PropTypes.func.isRequired,
  updateProfile: PropTypes.func.isRequired,
  setUser: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, {
  setCategory,
  setAlert,
  updateProfile,
  setUser,
})(Profile);
